<script>
import Stats from "./widgets/Stats";
import downloadCsv from "download-csv";

const qs = require("qs");
const axios = require("axios");
export default {
  name: "Dashboard",
  components: {
    Stats,
  },
  data() {
    return {
      statInfo: [],
      cities_name: "",
      period_name: [
        { value: "day", label: "За день" },
        { value: "month", label: "За месяц" },
        { value: "year", label: "За год" },
      ],
      action_switcher_period: "day",
      cities_key: "",
      cities_arr: "",
      all_select: false,
      warning: false,
      success: false,
      alert_message: "",
      SettingsModal: false,
      NewValveModal: false,
      AcceptanceModal: false,
      SmsInput: false,
      secretCode: "",
      isSelect: false,
      action_switcher: "",
      action_switcher_city: "",
      id: "",
      tableItems: [],
      tableFields: [
        { key: "select", label: "Выбор", sorter: false, filter: false },
        { key: "status", label: "Статус клапана", filter: false },
        { key: "city", label: "Город " },
        { key: "district", label: "Район ", _classes: "text-center" },
        { key: "street", label: "Улица " },
        { key: "typeofbuilding", label: "Вид здания" },
        { key: "buildingnum", label: "Номер дома " },
        { key: "entrancenum", label: "Номер подъезда " },
        { key: "shaftnum", label: "Номер шахты " },
        { key: "floor", label: "Этаж" },
        { key: "aptoroffice", label: "Кв/офис" },
        { key: "waterVolume", label: "Объем воды" },
        { key: "valveID", label: "ID клапана" },
        { key: "settings", label: "Действия", sorter: false, filter: false },
      ],
      checkedValves: [],
      availableOptions: [
        { value: 0, label: "Не выбрано" },
        { value: 1, label: "Включить отмеченные" },
        { value: 2, label: "Отключить отмеченные" },
      ],
      setting: {},
      cities: [
        { value: null, label: "Выберите город" },
        { value: "1", label: "Алма-ата" },
        { value: "2", label: "Нур-Султан" },
        { value: "3", label: "Шымкент" },
        { value: "4", label: "Актобе" },
        { value: "5", label: "Караганда" },
        { value: "6", label: "Тараз" },
        { value: "7", label: "Павлодар" },
        { value: "8", label: "Усть-Каменогорск" },
        { value: "9", label: "Семей" },
        { value: "10", label: "Атырау" },
        { value: "11", label: "Костанай" },
        { value: "12", label: "Кызылорда" },
        { value: "13", label: "Уральск" },
        { value: "14", label: "Петропавловск" },
        { value: "15", label: "Темиртау" },
        { value: "16", label: "Актау" },
        { value: "17", label: "Туркестан" },
        { value: "18", label: "Кокшетау" },
        { value: "19", label: "Талдыкорган" },
        { value: "20", label: "Экибастуз" },
        { value: "21", label: "Рудный" },
      ],
      districts: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      streets: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      buildings: [
        { value: "Сарыаркинский", label: "Сарыаркинский" },
        { value: "Есиль", label: "Есиль" },
      ],
      houses: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      entrances: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      aptoroffices: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      shaftnums: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      floors: [
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
      types: [
        { value: null, label: "Выберите ..." },
        { value: 1, label: "Физ лицо" },
        { value: 2, label: "Юр лицо" },
      ],
      payments: [
        { value: null, label: "Выберите способ" },
        { value: 1, label: "1" },
        { value: 2, label: "2" },
      ],
    };
  },
  mounted() {
    let city = auth.cities();
    this.cities_name = city.cities_name;
    this.cities_arr = city.cities;
    this.cities_key = city.cities_key;
    this.getResults(1);
    this.interval = setInterval(() => this.getResults(1), 30000);
  },
  methods: {
    tableStats(val){
      if (val === 1){
        this.tableItems = this.statInfo[1]
      }
      else if (val === 2){
        this.tableItems = this.statInfo[2]
      }
      else {
        this.tableItems = this.statInfo[4]
      }
    },
    showSettingModal(item) {
      this.setting = item;
      this.SettingsModal = !this.SettingsModal;
    },
    addNewValveModal() {
      this.setting = {};
      this.NewValveModal = !this.NewValveModal;
    },
    downloadArray() {
      const datas = this.tableItems;
      const columns = {
        select: "Выбор",
        status: "Статус клапана",
        city: "Город ",
        district: "Район ",
        street: "Улица ",
        typeofbuilding: "Вид здания",
        buildingnum: "Номер дома ",
        entrancenum: "Номер подъезда ",
        shaftnum: "Номер шахты ",
        floor: "Этаж",
        aptoroffice: "Кв/офис",
        valveID: "ID клапана",
        waterVolume: "Объем воды",
        settings: "Действия",
      };
      const current = new Date();
      const date = `${current.getDate()}-${
        current.getMonth() + 1
      }-${current.getFullYear()}`;
      let name = "";
      if (this.action_switcher_city.length >= 1) {
        name =
          "Клапаны в городе" + this.action_switcher_city + " " + date + ".csv";
      } else {
        name = "Клапаны в городе" + " Алма-Ата " + date + ".csv";
      }
      downloadCsv(datas, columns, name);
    },
    allSelect() {
      let app = this;
      app.isSelect = true;
      for (var i = 0; i < app.tableItems.length; i++) {
        if (app.all_select == false) {
          this.tableItems[i].select = true;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        } else {
          this.tableItems[i].select = false;
          this.tableItems[i].street = this.tableItems[i].street + " ";
        }
      }
      app.all_select = app.all_select == true ? false : true;
    },
    submitForm() {
      event.preventDefault();
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/addvalve/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно добавлено";
          if (this.cities_arr[this.action_switcher_city] == undefined) {
            this.getResults(1);
          } else {
            this.getResults(this.cities_arr[this.action_switcher_city]);
          }
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    editForm() {
      event.preventDefault();
      let app = this;
      console.log(this.setting);
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/updatevalve/",
        data: qs.stringify(this.setting),
      })
        .then(({ data }) => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно изменено";
          if (this.cities_arr[this.action_switcher_city] == undefined) {
            this.getResults(1);
          } else {
            this.getResults(this.cities_arr[this.action_switcher_city]);
          }
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    actionSelectChange(box) {
      let app = this;
      app.isSelect = true;
      if (box.select == false) {
        this.checkedValves = this.checkedValves.filter(
          (item) => item !== box.valveID
        );
      } else {
        this.checkedValves.push(box.valveID);
      }
    },
    sendAcceptance() {
      this.SmsInput = true;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/getsecretcode/")
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          if (error.response.status === 401) {
            window.auth.logout();
          }
        });
    },
    actionSwitcher() {
      if (this.checkedValves.length >= 2) {
        this.AcceptanceModal = !this.AcceptanceModal;
      }
    },
    actOnValves() {
      console.log(this.action_switcher);
      if (this.action_switcher === 1) {
        let app = this;
        axios.defaults.headers.common["Authorization"] =
          "Bearer " + window.auth.token;
        axios({
          method: "post",
          url: "/turnonmanyvalves/",
          data: qs.stringify({
            secret_code: this.secretCode,
            device_ids: this.checkedValves.join(),
          }),
        })
          .then(() => {
            app.success = true;
            app.alert_message = "Успешно включено";
            this.AcceptanceModal = false;
            this.action_switcher = 0;
            setTimeout(function () {
              app.success = false;
            }, 3000);
          })
          .catch(function (error) {
            app.warning = true;
            app.alert_message = error.response.data.detail;
          });
      } else {
        let app = this;
        axios.defaults.headers.common["Authorization"] =
          "Bearer " + window.auth.token;
        axios({
          method: "post",
          url: "/turnoffmanyvalves/",
          data: qs.stringify({
            secret_code: this.secretCode,
            device_ids: this.checkedValves.join(),
          }),
        })
          .then(() => {
            app.success = true;
            app.alert_message = "Успешно выключено";
            this.action_switcher = 0;
            this.AcceptanceModal = false;
            setTimeout(function () {
              app.success = false;
            }, 3000);
          })
          .catch(function (error) {
            app.warning = true;
            app.alert_message = error.response.data.detail;
          });
      }
    },
    actionSwitcherCity() {
      if (this.cities_arr[this.action_switcher_city] == undefined) {
        this.getResults(1);
      } else {
        this.getResults(this.cities_arr[this.action_switcher_city]);
      }
    },
    getResults(city) {
      let app = this;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/getvalvesbycity/" + city + "/" + this.action_switcher_period)
        .then((response) => {
          app.tableItems = response.data[0];
          this.statInfo = response.data;
          for (var i = 0; i < app.tableItems.length; i++) {
            app.tableItems[i].select = false;
          }
        })
        .catch((error) => {
          if (error.response.status === 401) {
            window.auth.logout();
          }
        });
    },
    actionStatusChange(item) {
      console.log(this.action_switcher_city);
      console.log("GEGEG");
      let app = this;
      var status =
        item.status == 1 || item.status == 2 ? "/turnon/" : "/turnoff/";
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: status,
        data: qs.stringify({
          device_id: item.valveID,
        }),
      })
        .then(({ data }) => {
          app.success = true;
          app.alert_message = data.responce;
          setTimeout(function () {
            app.success = false;
          }, 3000);

          if (this.cities_arr[this.action_switcher_city] == undefined) {
            this.getResults(1);
          } else {
            this.getResults(this.cities_arr[this.action_switcher_city]);
          }
        })
        .catch(function (error) {});
    },
    colorStatus(value) {
      let color = "secondary";
      if (value == 1) {
        color = "secondary";
      } else if (value == 2 || value == 4) {
        color = "warning";
      } else if (value == 3) {
        color = "danger";
      } else if (value == 5) {
        color = "success";
      }
      return color;
    },
    textStatus(value) {
      var color = "Выключен";
      if (value == 1) {
        color = "Отключен";
      } else if (value == 2) {
        color = "Отключается";
      } else if (value == 3) {
        color = "Сломался";
      } else if (value == 4) {
        color = "Включается";
      } else if (value == 5) {
        color = "Включен";
      } else {
        color = "Выключен";
      }
      return color;
    },
    color(value) {
      let $color;
      if (value <= 25) {
        $color = "info";
      } else if (value > 25 && value <= 50) {
        $color = "success";
      } else if (value > 50 && value <= 75) {
        $color = "warning";
      } else if (value > 75 && value <= 100) {
        $color = "danger";
      }
      return $color;
    },
  },
};
</script>
